  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  width: 100%;
  padding: 20vh 0 1rem 0;
}
.header__heading {
  font-size: 2rem;
  margin-bottom: 3rem;
  text-align: center;
}
.header__inputs {
  width: 80%;
  align-items: center;
  text-align: center;
}

.header__input {
  display: inline-block;
  margin: 0 1rem 1rem 0;
}

@media (min-width: 768px) {
  .header__heading {
    font-size: 4rem;
    margin-bottom: 2rem;
  }
  .header__input {
    margin: 0 2rem 2rem 0;
  }
}
